.app-wrapper {
  display: grid;
  max-width: 1000px;
  margin: 10px auto;
  grid-template-areas: "nav content content";

  grid-template-columns: 1fr 10fr;
  grid-gap: 10px;
  font-family: Helvetica, sans-serif;
}

.app-wrapper-content {
  grid-area: content;
  background-color: #fff;
}
